/* navigation bar start */
:root {
  color: white;
  background-color: rgb(27, 27, 27);
  font-family: Roboto;
  min-width: 350px;
}

.navbar {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: rgb(27, 27, 27);
  display: flex;
  justify-content: center;
  gap: 5rem;
  padding-block: 1rem;
  border-bottom: 0.1vh solid rgb(255, 255, 255);
}

/*Link*/
a {
  text-decoration: none;
  text-align: center;
  font-family: inherit;
  font-weight: bold;
  font-size: 1rem;
  color: inherit;
}
/* navigation bar end */

.main-area {
  background-color: inherit;
  display: grid;
}

.logo {
  display: flex;
  justify-content: center;
  font-size: clamp(1rem, calc(10px + 2vmin), 36px);
  font-family: inherit;
}

.logo-divider {
  border-top: 2px solid white;
  justify-self: center;
}

.content {
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  display: grid;
  gap: 1rem;
}

.content-column {
  background-color: #3d3d3d;
  padding-left: 2%;
  font-family: inherit;
  border-top-right-radius: .5rem;
  border-style: hidden;
  transition: background-color 1000ms ease;
}

.content-column:hover {
  background-color: #4b4a4a;
  border-color: #4b4a4a;
}

.content-column>h1 {
  line-height: 1;
  margin-bottom: 0;
  font-weight: 100;
  font-family: PlayfairDisplay;
}

@media (min-width: 50em) {
  .content {
    grid-template-columns: 1fr 1fr 1fr;
    margin-left: 5%;
    margin-right: 5%;
  }

  .content-column {
    padding-bottom: 2%;
  }
}

.columnDivider {
  border-image: linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255)) 1;
  width: 80%;
  margin-left: 0;
}

.link-container {
  /* position: bottom; */
  font-family: inherit;
  display: grid;
  justify-items: center;
  color: white;
  text-align: center;
  /* flex-direction: column; */
}